<template>
<el-dialog :width="width" :visible.sync="show" v-if="show" :title="title" @opened="tableColumnSelectionInit('multipleTable')">
    <el-table ref="multipleTable" stripe :border="true" :data="alldata" height="50vh" @select-all="tableSelect">
        <el-table-column type="selection"></el-table-column>
        <el-table-column v-for="(item,index) in datatitle" :key="index" :prop="item.prop" :label="item.label" :width="item.width" :min-width="item.width" :formatter="tableDataFormatter"></el-table-column>
    </el-table>
    <div slot="footer">
        <el-button type="primary" @click.native="update()">提交</el-button>
        <el-button type="warning" @click.native="tableColumnSelectionInit('multipleTable')">重置</el-button>
    </div>
</el-dialog>
</template>

<!--
ref : multipleTable
-->

<script>
export default {
    data() {
        return {
            show: false,
            alldata: [],
            haddata: [],
            updateData: {
                addData: [],
                deleteData: []
            },
        }
    },
    props: {
        visible: { //显示状态
            default: false,
            type: Boolean
        },
        parems: {
            type: Object,
            default: ()=>{return {}}
        }, //数据请求时注入数据
        title: {
            type: String,
            default: ""
        }, //弹窗标题
        url: {
            type: String,
            default: ""
        }, //多选表格数据来源
        width: {
            type: String,
            default: "60%"
        }, //弹窗宽度
        datatitle: {
            type: Array,
            default: () => {
                return []
            }
        }, //表格行标题
    },
    mounted() {
        this.show = this.visible;
        if(this.show){
            this.logindata();
        }
    },
    watch: {
        visible(news) {
            this.show = news;
        },
        show(news) {
            this.$emit("update:visible", news);
        },
        'parems'(news) {
            // this.parems = news;
            if(this.show){
            this.logindata();
        }
        }
    },
    methods: {
        logindata() {
            let vm = this;
            // let bool = vm.utils.opreate.isEmptyObject(vm.parems);
            // if (!bool) {
                vm.$axios.post(vm.url,vm.utils.data.formatter(vm.parems)).then(function (success) {
                        let res = success.data;
                        if (success.bool) {
                            vm.alldata = res.all;
                            vm.haddata = res.had;
                            vm.tableColumnSelectionInit('multipleTable');
                        }
                    })
                    .catch(function (err) {
                        console.log(err)
                    });
            // }
        },
        update() {
            let vm = this;
            vm.$axios.post(vm.updateurl, vm.utils.data.formatter(vm.updateData))
                .then(function (success) {
                    let res = success.data;
                    let bool = success.bool;
                    if (bool) {
                        vm.loginData();
                        vm.$message({
                            message: '修改成功',
                            type: 'success'
                        })
                    }
                })
                .catch(function (err) {
                    console.log(err);
                });
        },
        tableDataFormatter(row, column, cellValue, index) {
            if (cellValue == undefined || cellValue == "") {
                return "空"
            } else {
                return cellValue;
            }
        },
        //表格选中状态初始化
        tableColumnSelectionInit(ref) {
            let vm = this;
            let alldata = vm.alldata;
            let haddata = vm.haddata;
            alldata.forEach(items => {
                vm.$refs[ref].toggleRowSelection(items, false);
                haddata.forEach(item => {
                    if (items.id == item) {
                        vm.$refs[ref].toggleRowSelection(items, true);
                    }
                })
            })
        },
        //表格行选中状态修改
        tableColumnSelect(selection, row) {
            let vm = this;
            const haddata = vm.haddata;
            let deletedata = vm.updateData.deleteData;
            let adddata = vm.updateData.addData;
            const rowcheck = selection.includes(row);
            row.id += '';
            if (rowcheck) {
                if (deletedata.includes(row.id)) {
                    deletedata.splice(deletedata.indexOf(row.id), 1);
                }
                if (haddata.includes(row.id)) {
                    return;
                } else {

                    if (adddata.includes(row.id)) {
                        return;
                    } else {
                        adddata.push(row.id);
                    }
                }
            } else {
                if (adddata.includes(row.id)) {
                    adddata.splice(adddata.indexOf(row.id), 1);
                }
                if (haddata.includes(row.id)) {
                    if (!deletedata.includes(row.id)) {
                        deletedata.push(row.id);
                    } else {
                        return;
                    }
                }
            }
        },
        // 表格全部行选中状态修改
        tableSelect(selection) {
            let vm = this;
            let length = selection.length;
            const haddata = vm.haddata;
            let deletedata = vm.updateData.deleteData;
            let adddata = vm.updateData.addData;
            if (length > 0) {
                selection.forEach(item => {
                    item.id += '';
                    if (!haddata.includes(item.id)) {
                        if (!adddata.includes(item.id)) {
                            adddata.push(item.id);
                        }
                    }
                })
                deletedata.length = 0;
            } else {
                adddata.length = 0;
                deletedata.length = 0;
                deletedata.push(...haddata);
            }
        },
    },
}
</script>

